import React, { Component } from 'react';
import Modal from "./modal";

class AddBookButton extends Component {
  constructor(props) {
    super(props);
    this.state = {
      list: this.props.list,
      newItem: [{
        isbn: '',
        img: '',
        title: '',
        author: '',
        rating: '4',
        views: '2',
        free: '1',
        release: '2017',
        text: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.'
      }]
     }
  }

  handleTitleChange = (e) => {
    let enterValue = e.target.value;
    this.setState(prevState => ({
      newItem: {
          ...prevState.newItem,
          title: enterValue
      }
    }))
  };
  handleAuthorChange = (e) => {
    let enterValue = e.target.value;
    this.setState(prevState => ({
      newItem: {
          ...prevState.newItem,
          author: enterValue
      }
    }))
  };
  handleIsbnChange = (e) => {
    let enterValue = e.target.value;
    this.setState(prevState => ({
      newItem: {
          ...prevState.newItem,
          isbn: enterValue
      }
    }))
  };

  handleAddNewItem = () => {
    this.props.onChange(this.state.newItem);
  };

  render() { 
    return (
      <div className="add_book_wrapper">
        <Modal list={this.props.list} ref="modalAddBook" titleForModal="Add New Books">
        <div className="clearfix">
          <div className="modal_tabs">
            <ul>
              <li>
                <a href="http://" target="_blank" rel="noopener noreferrer" className="active">General</a>
              </li>
              <li>
                <a href="http://" target="_blank" rel="noopener noreferrer">Genre</a>
              </li>
              <li>
                <a href="http://" target="_blank" rel="noopener noreferrer">Poster</a>
              </li>
              <li>
                <a href="http://" target="_blank" rel="noopener noreferrer">Info</a>
              </li>
            </ul>
          </div>
            <div className="main_modal_content">
              <form action="">
                <label>Title<span className="red_stars">*</span></label>
                <input type="text" name="title" className="modal_text_input" placeholder="Enter Title" value={this.state.newItem.title || ''} onChange={this.handleTitleChange}/>
                <label>Author<span className="red_stars">*</span></label>
                <input type="text" name="author" className="modal_text_input" placeholder="Enter Author" value={this.state.newItem.author || ''} onChange={this.handleAuthorChange}/>
                <label>Publisher*</label>
                <input type="text" name="publisher" className="modal_text_input" placeholder="Enter Publisher"/>
                <div className="two_inputs_line">
                  <div className="inputs_col">
                  <label>Paperback</label>
                  <input type="text" name="paperback" className="modal_text_input" placeholder="Enter Paperback" />
                  </div>
                  <div className="inputs_col">
                  <label>ISBN<span className="red_stars">*</span></label>
                  <input type="text" name="isbn" className="modal_text_input" placeholder="Enter ISBN" value={this.state.newItem.isbn || ''} onChange={this.handleIsbnChange}/>
                  </div>
                </div>
                <label>Summary</label>
                <input type="text" name="summary" className="modal_text_input" placeholder="Enter Summary" />
              </form>
            </div>
          </div>
            <div className="modal_footer">
              <button className="modal_cancel_button" onClick={() => this.refs["modalAddBook"].hideModal()}>Cancel</button>
              <button className="modal_addbook_button" onClick={this.handleAddNewItem}>Add Book</button>
            </div>
        </Modal>

        <button className="add_book_btn" onClick={() => this.refs["modalAddBook"].handleShowModal()}>ADD A BOOK</button>
      </div>
     );
  }
}

 
export default AddBookButton;